/* Animation styles for the Solana Slot Game */

@keyframes spin {
  0% { transform: translateY(-500%); }
  100% { transform: translateY(500%); }
}

@keyframes pop {
  0% { transform: scale(0.8); opacity: 0.5; }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); opacity: 1; }
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

@keyframes glow {
  0% { box-shadow: 0 0 5px rgba(153, 69, 255, 0.5); }
  50% { box-shadow: 0 0 20px rgba(153, 69, 255, 0.8); }
  100% { box-shadow: 0 0 5px rgba(153, 69, 255, 0.5); }
}

@keyframes confetti {
  0% { transform: translateY(0) rotate(0deg); opacity: 1; }
  100% { transform: translateY(1000%) rotate(720deg); opacity: 0; }
}

@keyframes shimmer {
  0% { background-position: -100% 0; }
  100% { background-position: 200% 0; }
}

.shine {
  position: relative;
  overflow: hidden;
}

.shine::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: rotate(30deg);
  animation: shimmer 2s infinite;
  pointer-events: none;
}

.win-pulse {
  animation: pulse 0.5s ease infinite;
}

.glow {
  animation: glow 1.5s ease infinite;
}

.confetti {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  animation: confetti 2s ease-out forwards;
}